import React from 'react';
import Teacher from '../Teacher/Teacher';
import { Col, Row } from 'antd';
const TeacherList = ({ teachers }) => {
  return (
    <div>
    <div className='courseList'>
        <Row>
          {
            teachers.map((course, i) => {
              return (
                <Col sm={12} md={8} lg={6} xl={4}  key={teachers[i].id}>
                  <Teacher
                    key={teachers[i].id}
                    id={teachers[i].id}
                    name={teachers[i].name}
                    goal={teachers[i].goal}
                    thumbnail={teachers[i].thumbnail}
                  />
                </Col>
              );
            })
          }
        </Row>
    </div>
    </div>
  );
}

export default TeacherList;