import React, { Component } from 'react';
import './App.css';
import { Layout } from 'antd';
import AppHeader from './components/AppHeader/AppHeader';
import AppFooter from './components/AppFooter/AppFooter';
import Error from './components/Error/Error';
import Banner from './components/Banner/Banner';
import SearchField from './components/SearchField/SearchField';

import CourseList from './components/CourseList/CourseList';
import TeacherList from './components/TeacherList/TeacherList';
import ErrorBoundaries from './components/ErrorBoundaries/ErrorBoundaries';
import { connect } from 'react-redux';
import { setSearchField, requestCourses } from './redux/actions';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
const { Content } = Layout;
const mapStateToProps = state => {
  return {
    searchField: state.searchCourses.searchField,
    courses: state.requestCourses.courses,
    isPending: state.requestCourses.isPending,
    error: state.requestCourses.error
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    onSearchChange: (event) => dispatch(setSearchField(event.target.value)),
    onRequestCourses: () => dispatch(requestCourses())
  }
}
class App extends Component {
  constructor() {
    super()
    this.state = {
      teachers: []
    }
  }
  componentDidMount() {
    this.props.onRequestCourses();
    fetch('https://easy-mock.com/mock/5cb55a1c93eb412c8916b994/react_web/teachers')
      .then(response => response.json())
      .then(teacherData => {
        this.setState({ teachers: teacherData })
      });

  }

  render() {
    const { searchField, onSearchChange, courses } = this.props;
    const filterCourses = courses.filter(course => {
      return course.name.toLowerCase().includes(searchField.toLowerCase());
    });
    const teachers = this.state.teachers;
    return (
      <div className="App">
        <Layout>
          <BrowserRouter>
            <AppHeader />
            <Banner />
            <div style={{ background: '#ECECEC', padding: '10px' }}>
              <Content>

                <div>
                  <Switch>
                    <Route exact path="/demo" render={(props) => (
                      <ErrorBoundaries>
                        <CourseList courses={filterCourses} />
                      </ErrorBoundaries>
                    )} />
                    <Route path="/course" render={(props) => (
                      <ErrorBoundaries>
                        <div>
                          <SearchField searchChange={onSearchChange} />
                          <CourseList courses={filterCourses} />
                        </div>

                      </ErrorBoundaries>
                    )} />
                    <Route path="/teachers" render={(props) => (
                      <ErrorBoundaries><TeacherList teachers={teachers} /></ErrorBoundaries>

                    )} />
                    <Route component={Error} />
                  </Switch>
                </div>
              </Content>
            </div>
          </BrowserRouter>
          < AppFooter />
        </Layout>
      </div>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
